<template>
  <div class="zd-common-component">
    <div class="zd-common-clearFix">
      <Button type="primary" class="zd-common-fl zd-common-pl11 zd-common-pr11 zd-common-mr10" custom-icon="zd-common-icon-add" @click="showAddModal">
        物品档案
      </Button>
      <Button type="primary" class="zd-common-fl zd-common-pl11 zd-common-pr11 zd-common-mr10"
              custom-icon="zd-common-icon-import" @click="importHandle">
        导入
      </Button>
      <Button type="primary" class="zd-common-fl zd-common-pl11 zd-common-pr11 zd-common-mr10" @click="exportHandle">
        <Icon type="ios-open-outline" class="zd-common-fs18 zd-common-mr5 zd-common-fl"/>导出
      </Button>
      <Button type="primary" class="zd-common-fl zd-common-pl11 zd-common-pr11 zd-common-mr10" @click="exportQR">
        <Icon type="ios-open-outline" class="zd-common-fs18 zd-common-mr5 zd-common-fl"/>导出二维码
      </Button>
      <div class="zd-common-fr">
        <Input :maxlength="30" v-model="goodsName" placeholder="物品名称"
               class="zd-common-input-h38 zd-common-w200 zd-common-fl zd-common-mr10"/>
        <Button type="primary" class="zd-common-fl" @click="selectHandle" :loading="selectLoading">
          <span v-if="!selectLoading">查询</span>
          <span v-else>正在查询...</span>
        </Button>
      </div>
    </div>
    <div class="zd-common-mt16">
      <Table ref="currentRowTable" border :columns="columns" :data="data" @on-selection-change="selectAllHandle"></Table>
      <div class="zd-common-tc zd-common-mt20">
        <Page :total="total" :current="pageNum" :page-size="pageSize" @on-change="changePage" @on-page-size-change="changePageSize" show-sizer show-elevator show-total></Page>
      </div>
    </div>
    <!--添加物品档案-->
    <Modal v-model="addModal" :mask-closable="false" :width="920" @on-cancel="resetHandle">
      <h3 slot="header" class="zd-common-modal-header">
        <span>{{ modalTitle }}</span>
      </h3>
      <Form ref="addForm" :model="addForm" :rules="ruleValidate" :label-width="100" inline>
        <FormItem label="物品名称："  prop="name">
          <Input v-model.trim="addForm.name" placeholder="请输入物品名称" :maxlength="50" class="zd-common-w300"
                 @on-blur="transCode"></Input>
        </FormItem>
        <FormItem label="物品编码："  prop="code">
          <Input v-model.trim="addForm.code" placeholder="请输入物品编码" :maxlength="20" class="zd-common-w300"></Input>
        </FormItem>
        <FormItem label="物品分类："  prop="classify">
          <!--<Input v-model.trim="addForm.classify" placeholder="请选择物品分类" :maxlength="30" class="zd-common-w300"></Input>-->
          <treeselect v-model="addForm.classify" :multiple="false" :options="treeOptions" :normalizer="normalizer" noOptionsText="暂无数据"
            class="zd-common-w300" placeholder="请选择物品分类"  @close="closeOnSelect"/>
        </FormItem>
        <FormItem label="品牌："  prop="brand">
          <Input v-model.trim="addForm.brand" placeholder="请输入品牌" :maxlength="50" class="zd-common-w300"></Input>
        </FormItem>
        <FormItem label="规格/型号：" prop="model">
          <Input v-model.trim="addForm.model" placeholder="请输入规格/型号" :maxlength="50" class="zd-common-w300"></Input>
        </FormItem>
        <FormItem label="单位："  prop="unit">
          <Input v-model.trim="addForm.unit" placeholder="请输入单位" :maxlength="50" class="zd-common-w300"></Input>
        </FormItem>
        <FormItem label="最高库存：" prop="maxInventory">
          <InputNumber v-model.trim="addForm.maxInventory" placeholder="请输入最高库存" :precision="0" :min="1" :max="10000" class="zd-common-w300"></InputNumber>
        </FormItem>
        <FormItem label="最低库存：" prop="minInventory">
          <InputNumber v-model.trim="addForm.minInventory" placeholder="请输入最低库存" :precision="0" :min="1" :max="10000"
                       class="zd-common-w300"></InputNumber>
        </FormItem>
      </Form>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8"
                @click="handleSubmit('addForm')"
                :loading="saveLoading">
          <span v-if="!saveLoading">确定</span>
          <span v-else>正在保存...</span>
        </Button>
        <Button @click="resetHandle" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
    <!--删除-->
    <Modal v-model="delModal" :mask-closable="false" :width="360">
      <h3 slot="header"></h3>
      <div class="zd-common-tc">
        <div class="zd-common-modal-waring zd-common-auto"></div>
        <p class="zd-common-primary-c zd-common-font-size-large zd-common-mt15">是否确认删除？</p>
      </div>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr24 zd-common-w100 zd-common-h32 zd-common-lh8"
                :loading="saveLoading" @click="confirmDelHandle">
          <span v-if="!saveLoading">确定</span>
          <span v-else>正在删除...</span>
        </Button>
        <Button @click="delModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
    <!--查看-->
    <Modal v-model="viewModal" :mask-closable="false" :width="920">
      <h3 slot="header" class="zd-common-modal-header">
        <span>查看物品档案</span>
      </h3>
      <div class="zd-common-clearFix">
        <div class="zd-common-fl zd-common-w300 zd-common-tc">
          <qrcode-vue :value="allDetail.name+'('+allDetail.code+')'" size="170" level="H"></qrcode-vue>
        </div>
        <div class="zd-common-fl zd-cm-gf-detail">
          <div>
            <p>
              <span>物品名称：</span>
              <span>{{ allDetail.name }}</span>
            </p>
            <p>
              <span>物品编码：</span>
              <span>{{ allDetail.code }}</span>
            </p>
          </div>
          <div>
            <p>
              <span>物品分类：</span>
              <span>{{ allDetail.goodsKindName }}</span>
            </p>
            <p>
              <span>品牌：</span>
              <span>{{ allDetail.brand }}</span>
            </p>
          </div>
          <div>
            <p>
              <span>规格/型号：</span>
              <span>{{ allDetail.size }}</span>
            </p>
            <p>
              <span>单位：</span>
              <span>{{ allDetail.unit }}</span>
            </p>
          </div>
          <div>
            <p>
              <span>最高库存：</span>
              <span>{{ allDetail.maxInventory }}</span>
            </p>
            <p>
              <span>最低库存：</span>
              <span>{{ allDetail.minInventory }}</span>
            </p>
          </div>
        </div>
      </div>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" @click="viewModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">确定</Button>
        <Button @click="viewModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
    <!--导入-->
    <Modal v-model="importModal" :mask-closable="false">
      <h3 slot="header" class="zd-common-modal-header">
        <span>导入物品档案</span>
      </h3>
      <Form ref="importForm" :model="importForm" :rules="ruleValidate" :label-width="100" inline>
        <FormItem label="上传文件："  prop="fileName">
          <!--:action="serverPath + '/sr/storeroomData!doImportGoodsSupplier.action'"-->
          <Upload
            ref="upload"
            action="/sr/storeroomData!doImportGoodsSupplier.action"
            accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
            :before-upload="handleUpload"
          >
            <Button type="primary" icon="ios-cloud-upload-outline">选择文件</Button>
          </Upload>
          <div v-if="importForm.file !== null"> {{ importForm.file.name }} </div>
          <a :href="serverPath+'/sr/storeroomData!downloadGoodsInfoTemplate.action'">下载模板</a>
          <input type="text" v-model="importForm.fileName" hidden/>
        </FormItem>
      </Form>
      <import-msg :importErrorMsg="importErrorMsg"></import-msg>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr35 zd-common-w100 zd-common-h32 zd-common-lh8"
                @click="importSubmit('importForm')" :loading="saveLoading">
          <span v-if="!saveLoading">确定</span>
          <span v-else>正在保存...</span>
        </Button>
        <Button @click="importModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
    <!--导出-->
    <Modal v-model="exportModal" :mask-closable="false">
      <h3 slot="header" class="zd-common-modal-header">
        <span>导出物品档案</span>
      </h3>
      <div>
        <div style="border-bottom: 1px solid #e9e9e9;padding-bottom:6px;margin-bottom:6px;">
          <Checkbox
            :indeterminate="indeterminate"
            :value="checkAll"
            @click.prevent.native="handleCheckAll">全选</Checkbox>
        </div>
        <CheckboxGroup v-model="checkAllGroup" @on-change="checkAllGroupChange">
          <Checkbox label="1">物品名称</Checkbox>
          <Checkbox label="2">物品编码</Checkbox>
          <Checkbox label="3">物品分类</Checkbox>
        </CheckboxGroup>
      </div>
      <div slot="footer" class="zd-common-modal-footer zd-common-pt10">
        <Button type="primary" class="zd-common-mr24 zd-common-w100 zd-common-h32 zd-common-lh8" :loading="saveLoading">
          <span v-if="!saveLoading">确定</span>
          <span v-else>正在删除...</span>
        </Button>
        <Button @click="exportModal = false" class="zd-common-w100 zd-common-h32 zd-common-lh8">取消</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import Treeselect from '@riophae/vue-treeselect'
  import '@riophae/vue-treeselect/dist/vue-treeselect.css'
  import QrcodeVue from 'qrcode.vue';
  import {transformPY,utils} from "../../../comm/script/utils";
  import ImportMsg from '../../components/importMsg';
  export default {
    components: {
      QrcodeVue,
      Treeselect,
      ImportMsg
    },
    data() {
      const validateName = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('不能为空'));
        } else {
          BaseAxios(
            {
              method: 'post',
              url: '/sr/storeroomData!validateGoodsSupplierName.action',
              data: {
                validateValue:value,
                excludeValue:this.oldName
              }
            })
            .then((response) => {
              if(!response.data){
                callback(new Error('名称重复'));
              }
              callback();
            })
            .catch((error) => {
              callback(new Error('验证失败'));
              console.log(error);
            });
        }
      }
      const validateCode = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('不能为空'));
        } else {
          if(utils.regCode.test(value)){
            BaseAxios(
              {
                method: 'post',
                url: '/sr/storeroomData!validateGoodsInfoCode.action',
                data: {
                  validateValue:value,
                  excludeValue:this.oldCode
                }
              })
              .then((response) => {
                if(!response.data){
                  callback(new Error('编码重复'));
                }
                callback();
              })
              .catch((error) => {
                callback(new Error('验证失败'));
                console.log(error);
              });
          }else{
            callback(new Error(utils.regCodeMsg));
          }
        }
      }
      return {
        //添加物品档案
        addModal:false,
        addForm:{
          name:'',
          code:'',
          classify:null,
          brand:'',
          model:'',
          unit:'',
          maxInventory:null,
          minInventory:null,
        },
        ruleValidate: {
          name: [
            { required: true, validator:validateName, trigger: 'blur' }
          ],
          code: [
            { required: true, validator:validateCode, trigger: 'blur' }
          ],
          classify: [
            { required: true, message:'不能为空', trigger: 'blur' }
          ],
          unit: [
            { required: true, message:'不能为空', trigger: 'blur' }
          ],
          fileName: [
            {required: true, message: '请选择文件', trigger: 'blur'}
          ],
        },
        treeOptions:[],
        normalizer(node) {
          return {
            id: node.id,
            label: node.name,
            children: node.children,
          }
        },
        modalTitle:'添加物品档案',
        editId:'',
        oldName:'',
        oldCode:'',
        //列表
        columns: [
          {
            type: 'index',
            align: 'center',
            title: '序号',
            width:65
          },
          {
            type: 'selection',
            align: 'center',
            width:65
          },
          {
            title: '物品名称',
            align: 'center',
            key: 'name',
            render:(h,params)=>{
              return h('div', [
                h('span', {
                  class:'zd-common-primary-c zd-common-cp',
                  on: {
                    click: () => {
                      this.showViewModal(params.row.id);
                    }
                  }
                }, params.row.name),
              ])
            }
          },
          {
            title: '物品编码',
            align: 'center',
            key: 'code'
          },
          {
            title: '物品分类',
            align: 'center',
            key: 'classify',
          },
          {
            title: '品牌',
            align: 'center',
            key: 'brand',
          },
          {
            title: '规格/型号',
            align: 'center',
            key: 'model',
          },
          {
            title: '单位',
            align: 'center',
            key: 'unit',
          },
          {
            title: '最高库存',
            align: 'center',
            key: 'maxInventory',
          },
          {
            title: '最低库存',
            align: 'center',
            key: 'minInventory',
          },
          {
            title: '操作',
            align: 'center',
            key: 'action',
            width:160,
            render: (h, params) => {
              return h('div', [
                h('Button', {
                  props:{
                    type:'primary',
                    ghost:true,
                  },
                  style: {
                    height: "28px",
                    padding:'0 15px 1px',
                    marginRight:'10px'
                  },
                  on: {
                    click: () => {
                      this.showEditModal(params.row.id);
                    }
                  }
                }, '编辑'),
                h('Button', {
                  props:{
                    type:'error',
                    ghost:true,
                  },
                  style: {
                    height: "28px",
                    padding:'0 15px 1px'
                  },
                  on: {
                    click: () => {
                      this.showDelModal(params.row.id);
                    }
                  }
                }, '删除')
              ]);
            }
          },
        ],
        data: [],
        pageSize:10,
        pageNum:1,
        total:0,
        selectLoading:false,
        //查询
        goodsName:'',
        saveLoading:false,
        //查看
        viewModal:false,
        allDetail:{},
        //删除
        delModal:false,
        //导入
        importModal:false,
        importForm:{
          file:null,
          fileName:''
        },
        importErrorMsg:[],
        //导出
        allId:'',
        exportModal:false,
        indeterminate: false,
        checkAll: false,
        checkAllGroup: []
      }
    },
    created(){
      this.getGoodsFilesList(this.pageNum,this.pageSize,this.goodsName);
      this.getKind();
    },
    methods: {
      //自动生成code
      transCode(){
        this.addForm.code = transformPY(this.addForm.name);
      },
      //获取分类
      getKind(){
        this.treeOptions= [];
        this.getGoodsKindTree().then((res)=>{
          this.treeOptions = res.data;
          // this.recursionFnOne(this.treeOptions,res.data);
        })
      },
      //关闭tree事件
      closeOnSelect(value, instanceId){
        this.addForm.classify = value;
        this.$refs['addForm'].validateField('classify');
      },
      //添加仓库档案
      showAddModal(){
        this.editId = '';
        this.oldCode = '';
        this.addModal = true;
        this.$refs['addForm'].resetFields();
      },
      saveOrUpdateGoodsInfo(name,code,goodsKind,brand,size,unit,maxInventory,minInventory,editId) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!saveOrUpdateGoodsInfo.action',
          data:{
            "goodsInfo.name":name,
            "goodsInfo.code":code,
            "goodsInfo.goodsKind.id":goodsKind,
            "goodsInfo.brand":brand,
            "goodsInfo.size":size,
            "goodsInfo.unit":unit,
            "goodsInfo.maxInventory":maxInventory,
            "goodsInfo.minInventory":minInventory,
            "goodsInfo.id":editId,
          }
        })
          .then((response) => {
            this.saveLoading=false;
            this.addModal=false;
            this.getGoodsFilesList(this.pageNum,this.pageSize,this.goodsName);
            this.postSuccessTip('保存成功');
          })
          .catch((error) => {
            this.saveLoading=false;
            this.addModal=false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //保存验证
      handleSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.saveLoading=true;
            this.saveOrUpdateGoodsInfo(this.addForm.name,this.addForm.code,this.addForm.classify,this.addForm.brand,this.addForm.model,this.addForm.unit,this.addForm.maxInventory,this.addForm.minInventory,this.editId);
          }
        })
      },
      //列表
      getGoodsFilesList(pageNum,pageSize,name) {
        this.data = [];
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getGoodsInfoPage.action',
          data:{
            pageNum:pageNum,
            pageSize:pageSize,
            name:name
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData && aData.list.length>0){
              aData.list.map((item)=>{
                this.data.push({
                  id:item.id,
                  name:item.name,
                  code:item.code,
                  classify:item.goodsKind,
                  brand:item.brand,
                  model:item.size,
                  unit:item.unit,
                  maxInventory:item.maxInventory,
                  minInventory:item.minInventory
                })
              })
            }
            this.total = aData.totalCount;
            this.selectLoading = false;
          })
          .catch((error) => {
            this.selectLoading = false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //分页
      changePage(num) {
        this.pageNum = num;
        this.getGoodsFilesList(this.pageNum,this.pageSize,this.goodsName);
      },
      changePageSize(num) {
        this.pageSize = num;
        this.getGoodsFilesList(this.pageNum,this.pageSize,this.goodsName);
      },
      //查询
      selectHandle(){
        this.selectLoading = true;
        this.getGoodsFilesList(this.pageNum,this.pageSize,this.goodsName);
      },
      //查看详情
      showViewModal(id){
        this.viewModal = true;
        this.getGoodsInfoDetail(id);
      },
      getGoodsInfoDetail(id) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!getGoodsInfoDetail.action',
          data:{
            id:id
          },
        })
          .then((response) => {
            let aData = response.data;
            this.allDetail = aData;
            this.addForm.name = aData.name;
            this.addForm.code = aData.code;
            this.oldCode = aData.code;
            this.addForm.classify = aData.goodsKindId;
            this.addForm.brand = aData.brand;
            this.addForm.model = aData.size;
            this.addForm.unit = aData.unit;
            this.addForm.maxInventory = aData.maxInventory;
            this.addForm.minInventory = aData.minInventory;
          })
          .catch((error) => {
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      //编辑
      showEditModal(id){
        this.editId = id;
        this.modalTitle = '编辑物品档案';
        this.addModal = true;
        this.getGoodsInfoDetail(id);
      },
      resetHandle(){
        this.$refs['addForm'].resetFields();
        this.addModal = false;
      },
      //删除
      showDelModal(id){
        this.editId = id;
        this.delModal = true;
      },
      deleteGoodsInfo(id) {
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!deleteGoodsInfo.action',
          data:{
            id:id
          },
        })
          .then((response) => {
            let aData = response.data;
            if(aData !== ''){
              this.postWarningTip(aData);
            }
            this.saveLoading = true;
            this.getGoodsFilesList(this.pageNum,this.pageSize,this.goodsName);
          })
          .catch((error) => {
            this.saveLoading = true;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },
      confirmDelHandle(){
        this.delModal = false;
        this.saveLoading = false;
        this.deleteGoodsInfo(this.editId);
      },
      //导入
      importHandle(){
        this.importErrorMsg = [];
        this.importModal = true;
        this.$refs['importForm'].resetFields();
        this.importForm.file = null;
      },
      handleUpload (file) {
        if(!utils.regImportType.test(file.name)){
          this.postWarningTip(utils.regImportTypeMsg);
        }else{
          this.importForm.file = file;
          this.importForm.fileName = file.name;
          this.$refs['importForm'].validateField('fileName');
        }
        return false;
      },
      importSubmit(name) {
        this.$refs[name].validate((valid) => {
          if (valid) {
            this.saveLoading=true;
            this.validateImportGoodsInfo();
          }
        })
      },
      saveImport() {
        let formData = new FormData();
        formData.append('upload',this.importForm.file);
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!doImportGoodsInfo.action',
          data:formData,
          processData: false,
          contentType: false,
          transformRequest: [function (data) {
            return data;
          }],
        })
          .then((response) => {
            this.saveLoading=false;
            this.importModal=false;
            this.importForm.file = null;
            this.getGoodsFilesList(this.pageNum,this.pageSize,this.goodsName);
          })
          .catch((error) => {
            this.saveLoading=false;
            this.importModal=false;
            this.postErrorTip('请求失败');
            this.$refs.upload.value = '';
            console.log(error);
          });
      },
      //导入前验证
      validateImportGoodsInfo() {
        let formData = new FormData();
        formData.append('upload',this.importForm.file);
        BaseAxios({
          method: 'post',
          url: '/sr/storeroomData!validateImportGoodsInfo.action',
          data:formData,
          processData: false,
          contentType: false,
          transformRequest: [function (data) {
            return data;
          }],
        })
          .then((response) => {
            const aData = response.data;
            this.importErrorMsg = aData;
            if(aData.length<=0){
              this.saveImport();
            }else{
              this.saveLoading=false;
            }
          })
          .catch((error) => {
            this.saveLoading=false;
            this.postErrorTip('请求失败');
            console.log(error);
          });
      },

      //导出
      exportHandle(){
        location.href = this.serverPath+"/sr/storeroomData!exportGoodsInfo.action?ids="+this.allId.join(',');
      },
      selectAllHandle(selection){
        this.allId = selection.map((item)=>{
          return item.id;
        });
      },
      //=======暂未使用==========
      handleCheckAll() {
        if (this.indeterminate) {
          this.checkAll = false;
        } else {
          this.checkAll = !this.checkAll;
        }
        this.indeterminate = false;

        if (this.checkAll) {
          this.checkAllGroup = ['1','2','3'];
        } else {
          this.checkAllGroup = [];
        }
      },
      checkAllGroupChange (data) {
        if (data.length === 3) {
          this.indeterminate = false;
          this.checkAll = true;
        } else if (data.length > 0) {
          this.indeterminate = true;
          this.checkAll = false;
        } else {
          this.indeterminate = false;
          this.checkAll = false;
        }
      },
      //=================
      //导出二维码
      exportQR(){
        location.href = this.serverPath+"/sr/storeroomData!exportGoodsInfoQRCode.action?ids="+this.allId.join(',');
      }
    },
    mounted() {

    }
  }
</script>
<style lang="less" scoped>
  .ivu-table .demo-table-info-row td{
    background-color: rgba(28, 144, 243, 0.1);
  }
  .zd-cm-gf-detail{
    width: 500px;
  }
  .zd-cm-gf-detail div{
    clear: both;
  }
  .zd-cm-gf-detail p{
    width: 50%;
    margin:10px 0 15px;
    float: left;
  }
  .zd-cm-gf-detail p span:first-child{
    width: 100px;
    text-align: right;
    display: inline-block;
    float: left;
  }
  .zd-cm-gf-detail p span:last-child{
    width: 150px;
    display: inline-block;
    float: left;
  }
</style>



